<template>
	<div>
		<!-- start -->
		<g-header></g-header>
		<!-- end -->
		<!-- start -->
		<mt-swipe :auto="4000">
			<mt-swipe-item>
				<ul class="swipe-list">
    				<li style="background-color: #000">
						11111111
    				</li>	
				</ul>
			</mt-swipe-item>
			<mt-swipe-item>
				<ul class="swipe-list">
    				<li style="background-color: #eee">
						2222
    				</li>	
				</ul>
			</mt-swipe-item>
			<mt-swipe-item>
				<ul class="swipe-list">
			    	<li style="background-color: #ccc">
						33333
			    	</li>	
				</ul>
			</mt-swipe-item>
		</mt-swipe>
		<!-- end -->
		<!-- start -->
		<ul class="list">
			<li v-for="item in lists">
				<router-link :to="{ name: 'detail', params: { id: item.id }}">  {{item.id}}
        	</router-link>  
			
			</li>
		</ul>
		<!-- end -->
		<button type="" @click="toast_button" style="border:1px solid #ccc">toast</button>
		<!-- lazyload start-->
		<ul id="container">
			<li v-for="item in lists">
				<img v-lazy.container="item.image">
			</li>
		</ul>
		<!-- lazyload end-->
		<!-- start -->
		<ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" infinite-scroll-immediate-check="false">
		  <li v-for="item in lists">{{ item.name }}</li>
		</ul>
		<!-- end -->
		
		<!-- start -->
		<g-footer></g-footer>
		<!-- end -->
	</div>
	
</template>
<script>


// start
import header from '../../components/header.vue';
import footer from '../../components/footer.vue';
// end


import {Indicator} from 'mint-ui'; /**/
import { Swipe, SwipeItem } from 'mint-ui'; /**/
import { Toast } from 'mint-ui';/*toast*/

export default {
	data() {
		return {
			lists:[],
			allUseLoad : false,
			bottomText : "",
			pages :1,
			loading : false,
		}
	},

	created () {
		// 1.0
		this.get_data();
		
	},
	methods: {

		get_data: function(params) {
			var self = this
			if (!params) params = {
				name : 'nihao',
				age : 2,
			}
			self.$http.post('/test', [{
				params:{
					'name' : "nihao",
					'age':1,
				},
				dataType: 'json' 
			}])
			.then(function(res){
			    // 
			    console.log(res);
			    self.lists= res.data.list;
			}, function(response){
			    // 
			});
			
		},
		toast_button(){
			Toast('');
		},
		loadBottomUse() {
		  // 
		  console.log("load");
		  
		},
		loadMore(){
			console.log(this.pages);
			this.pages++;
			this.$http.post("/test",{
				params:{
					'page':this.pages,
					'tableNum':1,
					'justList':1
				}
			}).then(function(res){
				console.log(this.pages);
				if (res.data.errcode==0) {
					this.lists=(this.lists).concat(res.data.list);
				}
				else{
					this.loading=true;
				}
				
			},function(msg){
				
			})
		}
	},
	components : {
		'g-header' : header,
		'g-footer' : footer,
		'mt-swipe' : Swipe,
		'mt-swipe-item' : SwipeItem,
	}
}
</script>
<style lang="scss">
  @import "../../style/page/index";

</style>


